
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Divider from 'vux/src/components/divider/index.vue'

import selectimage from "../element/selectImage.vue"
import appfooter from "../element/footer.vue"
export default {
name: 'page',
directives: {
TransferDom
},
components: {
appfooter,
XHeader,
Group,
XInput,
XButton,
PopupPicker,
Datetime,
Cell,
XTextarea,
XNumber,
Checker,
CheckerItem,
XSwitch,
Popup,
selectimage,
XDialog,
ChinaAddressV4Data,
XAddress,
ButtonTab,
ButtonTabItem,
Divider
},
data() {
return {
title: "新增案例",
username: "",
stylize: "",
type: 1,
gotourl: "",
month: 0,
area: 0,
selectAddress: "",
value: [],
addressData: ChinaAddressV4Data,
showAddress: false,
detailedaddress: "",
childtab: 0,
showfullview:false,
sharestaff: false,
showToast: false,
actionStaffs: false,
selectType:false,
images: [],
fullimages: [],
cid: 0,
staffs: [],
residence:[],
leadername: "请选择作者",
leader_id: 1,
uid: 0,
rid:0,
ridname:"请选择楼盘人",
house:"",
showRidPopup:false


}
},
created() {

this.cid = this.util.getCid();
if(this.cid == null || this.cid == undefined) {
this.cid = 2;
}
this.initPro();
},
methods: {
initPro: function() {
let params = {
cid: this.cid
}
this.$axios.post(this.umcons.serviceAddress.CASE_SOURCE, params).then(function(response) {
var error = response.data.error;
if(error == 0) {
var data = response.data.data;

this.staffs = data.uids;
this.residence = data.residence;
console.log(data);
} else {
this.util.showAlert("加载失败");
}

}.bind(this));

},

onAddFollowUp: function() {
let params = {
title: this.username,
uid: this.uid,
cid: this.cid,
style: this.stylize,
type: this.type,
area: this.area,
photo: this.images[0],
rid: this.rid,
url:this.gotourl,
address: this.selectAddress+this.detailedaddress,
panorama: this.fullimages
}
console.log(params)
this.$axios.post(this.umcons.serviceAddress.CREATE_CASE, params).then(function(response) {
var error = response.data.error;
if(error == 0) {
var data = response.data.mes;
this.util.showAlert(data);

} else {
this.util.showAlert("加载失败");
}

}.bind(this));
},
logHide(str) {
console.log('on-hide', str)
},
logShow(str) {
console.log('on-show')
},
consoleIndex() {
console.log('click demo01', this.childtab)
},
onShadowChange(ids, names) {

console.log(names);
this.selectAddress = names[0] + names[1] + names[2];
},
onEvent(event) {
console.log('on', event);
},

change(value) {
console.log('change', value);
},
change(val, label) {
console.log('change', val, label)
},selectRid:function(index,name){
this.rid=index;
this.ridname=name;
this.showRidPopup=false;
},
imageCall: function(data) {
if(this.selectType){
this.fullimages = data;
}else{
this.images = data;

}

this.showToast = false;
console.log(data)
},selectImage:function(type){
this.selectType=type;
this.showToast = true;
},
onSelectPrincipal: function(id,namme) {
this.uid=id;
this.leadername=namme;
this.actionStaffs = false;
}
}
}



